import React, {memo} from "react";
import makeStyles from "@material-ui/core/styles/makeStyles";


const useStyles = makeStyles((theme) => ({
    title: {
        fontSize: '18px',
        fontWeight: 400,
    },
    redText: {
        color: '#FF0000',
        fontSize: '12px',
    },
    conditions: {
        marginRight: '10px',
        fontSize: '12px',
    },
    select: {
        fontSize: '12px',

    }

}));

const EventsListPageTitles = () => {
    const classes = useStyles();


    return <>
        <p className={classes.title}></p>
        <span className={classes.conditions}> </span>
        <span className={classes.select}> () </span>
        <span className={classes.redText}> </span>
    </>

}

export default memo(EventsListPageTitles)
